const LOCAL = {
    resourceUrls: {
        users: "http://localhost:8080/pheru-darts/users"
    }
};
const LOCAL_ERROR = {
    resourceUrls: {
        users: "http://localhost:8080/pheru-darts/testfehler"
    }
};
const PRODUCTION = {
    resourceUrls: {
        users: "TODO"
    }
};

const LOCAL_ERROR_MODE_STRING = "testmodefail";

export function getConfig() {
    switch (window.location.hostname) {
        case "localhost":
            if (window.location.search.includes(LOCAL_ERROR_MODE_STRING)) {
                return LOCAL_ERROR;
            }
            return LOCAL;
        default:
            return PRODUCTION;
    }
}




